<template>
  <div>
    <el-dialog
      :title="update == true ? '轮播图添加' : '轮播图修改'"
      :visible.sync="dialogFormVisible"
    >
      <el-form :model="form">
        <el-form-item label="轮播图标题" :label-width="formLabelWidth">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>

        <!-- 用户头像上传 -->
        <el-form-item label="图片" :label-width="formLabelWidth">
          <div class="box">
            <el-upload
              class="avatar-uploader"
              action="#"
              :show-file-list="false"
              :on-change="change"
            >
              <img v-if="imgurl" :src="imgurl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
        </el-form-item>

        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
            active-color="#13ce66"
            inactive-color="#ff4949"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="addbannerList" v-if="update"
          >添加</el-button
        >
        <el-button type="primary" @click="updatebanner" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { alertSuccess, alertWarn } from "../../../utils/alert";
import { banneradd, banneredit, bannerinfo } from "../../../utils/request";
export default {
  props: ["update"],
  components: {},
  data() {
    return {
      defaultProps: {
        children: "children",
        label: "title",
      },
      imgurl: "",
      dialogFormVisible: false,
      form: {
        title: "",
        img: "",
        status: 1,
      },
      formLabelWidth: "120px",
    };
  },
  computed: {
    ...mapGetters({
      bannerList: "bannerList",
    }),
  },
  methods: {
    ...mapActions({
      getbannerList: "getbannerList",
    }),
    // 上传图片失败执行的回调
    change(e) {
      let file = e.raw;
      this.imgurl = URL.createObjectURL(file);
      this.form.img = file;
    },
    cancle() {
      this.dialogFormVisible = false;
      this.clearForm();
    },
    clearForm() {
      this.form = {
        title: "",
        img: "",
        status: 1,
      };
    },
    addbannerList() {
      this.dialogFormVisible = false;
      banneradd(this.form).then((res) => {
        console.log(res);
        this.getbannerList();
        this.clearForm();
      });
    },
    getbannerDetail(id) {
      bannerinfo(id).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.imgurl = this.$imgurl + this.form.img;
      });
    },
    updatebanner() {
      banneredit(this.form).then((res) => {
        if (res.data.code == 200) {
          alertSuccess(res.data.msg);
          this.dialogFormVisible = false;
          this.clearForm();
          this.getbannerList();
        } else {
          alertWarn(res.data.msg);
        }
      });
    },
  },
  mounted() {
    this.getbannerList();
  },
};
</script>
<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.box {
  width: 178px;
  height: 178px;
  border: 1px dashed #ccc;
}
</style>


